/* Navbar compositions */
.nav-bar {
  display: flex;
  background: #0c0d39;
  box-shadow: 10px 0px 40px 20px #0c0c37;
  width: 170px;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
  min-height: 500px;
  /* Styles main logo */
  .logo {
    display: block;
  }
  /* Styles logo images */
  img {
    display: block;
    width: 45px;
    height: auto;

    &.sub-logo {
      width: 170px;
    }
  }
  .main-logo {
    margin: 40px 40px 20px 60px;
    // transform: rotateY(45deg);
    animation: rotateAnimation 1s linear;
  }
  @keyframes rotateAnimation {
    from {
      transform: rotateY(45deg);
    }
    to {
      transform: rotateY(225deg);
    }
  }
  /* Navbar icon styling */
  .icon {
    width: 30px;
  }
  nav {
    display: block;
    text-align: center;
    position: absolute;
    top: 50%;
    margin-top: -170px;
    width: 100%;
    /* Styles icons */
    a {
      font-size: 30px;
      color: white;
      display: block;
      line-height: 51px;
      height: 90px;
      position: relative;
      text-decoration: none;
      /* Transitions for icons */
      i {
        transition: all 0.3s ease-out;
      }
      /* Changes colour on hover */
      &:hover {
        color: #a1a4d2;

        svg {
          opacity: 0;
        }
        &:after {
          opacity: 1;
        }
      }
      /* Changes state after pressing for all icons */
      &:after {
        content: '';
        font-size: 22px;
        letter-spacing: 2px;
        position: absolute;
        bottom: 35px;
        display: block;
        width: 100%;
        text-align: center;
        opacity: 0;
        transition: all;
      }
      &:first-child {
        &::after {
          content: 'HOME';
        }
      }
    }
    /* Adds hyperlink text on hover */
    a.about-link {
      &:after {
        content: 'ABOUT';
      }
    }
    a.contact-link {
      &:after {
        content: 'CONTACT';
      }
    }
    a.portfolio-link {
      &:after {
        content: 'PORTFOLIO';
      }
    }
    a.active {
      svg {
        color: #8891ff;
        // color: white;
      }
    }
  }
  /* Styling for Github, Linkedin and Twitter icon links */
  ul {
    position: absolute;
    bottom: 20px;
    width: 100%;
    display: block;
    padding: 0;
    list-style: none;
    text-align: center;
    margin: 0;

    li {
      a {
        padding: 7px 0;
        display: block;
        font-size: 22px;
        line-height: 30px;
        /* Adds colour on hover */
        &:hover svg {
          color: white;
        }
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .nav-bar {
    width: 80px;
    box-shadow: 5px 0px 20px 10px #0c0c37;
    img {
      width: 35px;

      &.sub-logo {
        width: 90px;
      }
    }
    .main-logo {
      margin: 20px 30px 20px 28px;
      // transform: rotateY(45deg);
      animation: rotateAnimation 1s linear;
    }
    @keyframes rotateAnimation {
      from {
        transform: rotateY(45deg);
      }
      to {
        transform: rotateY(225deg);
      }
    }
    .icon {
      width: 25px;
    }
    nav {
      top: 55%;
      a {
        text-align: center;
        margin-top: 10px;
        height: 50px;
        &:after {
          margin-left: 5px;
          font-size: 15px;
          width: 100%;
          bottom: -5px;
        }
      }
    }
  }
}
